const initialReset = {
	flag: 0
}

export default function resetReducer (state = initialReset, action = null) {
	
	switch (action.type) {
		
		case 'CREATE_NEW_MAP':
			return {
				...state,
				flag: flag + 1
			}
		
		default:
			return state;
		
	}
	
	return state;
}


