import {ATTACK_VARIANCE, tileType, reverseLookup, weaponTypes, ENEMY, PLAYER, entityMap} from '../helpers/constants';
import React, {Component} from 'react';
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux';
import _ from 'lodash';
import ToggleButton from '../components/toggle_button';
import * as actions from '../actions/index';
import humane from 'humane-js';
//import jsdom from 'jsdom';
//nextProps = nextProps.state.state.entities.player

/*
notifier.error
 */

class Board extends Component {
    
    constructor(props) {
        super(props);
        this._handleKeypress = this._handleKeypress.bind(this);
        this.props.setWindowSize();
        // Setup humane toast notifiers
        const notifier = humane.create({baseCls: 'humane-jackedup', timeout: 5000});
        notifier.error = notifier.spawn({addnCls: 'humane-jackedup-error'});
        notifier.success = notifier.spawn({addnCls: 'humane-jackedup-success'});
        
    }
    
    componentWillMount() {
        const newState = this.props.state.state;
        
        this.setState(this._select2(newState, this.props.mapAlgo));
        
        var that = this;
        setTimeout(function(){that._setupGame()}, 1500);
    }
    
    componentWillReceiveProps(nextProps){
        const newState = nextProps.state.state;
        if (newState.entities.player.toNextLevel <= 0) this._playerLeveledUp();
        this.setState(this._select(newState));
    }
    
    _select(state) {
        return {
            player        : state.entities.player,
            entities      : state.entities,
            map           : state.map,
            occupiedSpaces: state.occupiedSpaces,
            level         : state.level,
            windowHeight  : state.windowHeight,
            windowWidth   : state.windowWidth,
            darkness      : state.darkness
        }
    }
    
    _select2(state, algoMap) {
        return {
            player        : state.entities.player,
            entities      : state.entities,
            map           : algoMap,
            occupiedSpaces: state.occupiedSpaces,
            level         : state.level,
            windowHeight  : state.windowHeight,
            windowWidth   : state.windowWidth,
            darkness      : state.darkness
        }
    }
    
    
    setWindowSize(e) {
        this.props.setWindowSize();
    }
    
    
    componentDidMount() {
        window.addEventListener('keydown', this._handleKeypress);
        window.addEventListener('resize', this.props.setWindowSize);
    }
    
    
    componentWillUnmount() {
        window.removeEventListener('keydown', this._handleKeypress);
        window.removeEventListener('resize', this.props.setWindowSize);
    }
    
    
    _playerLeveledUp() {
        const currLevel = this.state.player.level + 1;
        this.props.levelUp(currLevel * PLAYER.attack, currLevel * PLAYER.health,
          (currLevel + 1) * PLAYER.toNextLevel);
    }
    
    
    _setupGame() {
        const music = new Audio('http://www.tannerhelland.com/dmusic/Deeper.mp3');
        music.play();
        console.log("_setupGame just ran");
        console.log(this.props.mapAlgo);
        this.props.resetMap(this.props.mapAlgo);
        this._fillMap()
        this.props.setWindowSize();
        this.props.mapAlgo1();
    }
    
    _getEmptyCoords() {
        console.log("getEmptyCoords is running");
    
        const {map, occupiedSpaces} = this.state; // eh this should work.....
        let coords, x, y;
        do {
            x = Math.floor(Math.random() * map.length);
            y = Math.floor(Math.random() * map[0].length);
            if (map[x][y] === tileType.FLOOR && !occupiedSpaces[x + 'x' + y]) {
                coords = {x: x, y: y};
            }
        } while (!coords);
        return coords;
    }
    
    _fillMap() {
        this.props.setLocation('player', this._getEmptyCoords());
        // Place items
        const state  = this.state;
        const weapon = weaponTypes[this.state.level];
        this.props.addEntity(weapon.entityName, 'weapon', weapon.health, weapon.attack, this._getEmptyCoords());
        // Place heath and enemies
        const NUM_THINGS = 5,
              HEALTH_VAL = 20,
              LEVEL_MULT = state.level + 1;
        for (let i = 0; i < NUM_THINGS; i++) {
            this.props.addEntity('health' + i, 'health', HEALTH_VAL, 0, this._getEmptyCoords());
            this.props.addEntity('enemy' + i, 'enemy', LEVEL_MULT * ENEMY.health,
              LEVEL_MULT * ENEMY.attack, this._getEmptyCoords());
        }
        // Place exit if not last level
        if (state.level < 4) this.props.addEntity('exit', 'exit', 0, 0, this._getEmptyCoords());
        // Place boss on last (fifth) level
        if (state.level === 4) this.props.addBoss(125, 500, this._getEmptyCoords());
    }
   
    _addVector(coords, vector) {
        return {x: coords.x + vector.x, y: coords.y + vector.y};
    }
    
    _toggleDarkness() {
        this.props.toggleDarkness();
    }
    
    _handleKeypress(e) {
        console.log("_handleKeypress just ran");
        console.log(e.keyCode);
        let vector = '';
        switch (e.keyCode) {
            case 37:
                vector = {x: -1, y: 0};
                break;
            case 38:
                vector = {x: 0, y: -1};
                break;
            case 39:
                vector = {x: 1, y: 0};
                break;
            case 40:
                vector = {x: 0, y: 1};
                break;
            default:
                vector = '';
                break;
        }
        if (vector) {
            e.preventDefault();
            this._handleMove(vector);
        }
    }
    
    _handleMove(vector) {
        const state     = this.state;
        const player    = state.entities.player;
        console.log("_handleMove ran and the player's x coordinate is: " + this.props.state.state);
        const map       = state.map;
        const newCoords = this._addVector({x: player.x, y: player.y}, vector);
        console.log("The newCoords for y is: " + newCoords.y );
        if (newCoords.x > 0 && newCoords.y > 0 && newCoords.x < map.length &&
          newCoords.y < map[0].length &&
          map[newCoords.x][newCoords.y] !== tileType.WALL) {
            // Tile is not a wall, determine if it contains an entity
            const entityName = state.occupiedSpaces[newCoords.x + 'x' + newCoords.y];
            // move and return if empty
            if (!entityName) {
                this.props.move('player', vector);
                return;
            }
            // handle encounters with entities
            const entity = state.entities[entityName];
            switch (entity.entityType) {
                case 'weapon':
                    this.props.switchWeapon(entityName, entity.attack);
                    this.props.move('player', vector);
                    break;
                case 'boss':
                case 'enemy':
                    const playerAttack = Math.floor((Math.random() * ATTACK_VARIANCE) + player.attack - ATTACK_VARIANCE);
                    const enemyAttack  = Math.floor((Math.random() * ATTACK_VARIANCE) + entity.attack - ATTACK_VARIANCE);
                    // Will hit kill enemy?
                    if (entity.health > playerAttack) {
                        // Will rebound hit kill player?
                        if (enemyAttack > player.health) {
                            notifier.error('You died. Better luck next time!');
                            this._setupGame();
                            return;
                        }
                        this.props.damage(entityName, playerAttack);
                        this.props.damage('player', enemyAttack);
                    } else {
                        // Is the enemy a boss?
                        if (entityName === 'boss') {
                            notifier.success('A winner is you!');
                            this._setupGame();
                            return;
                        }
                        this.props.gainXp((state.level + 1) * ENEMY.xp);
                        this.props.removeEntity(entityName);
                    }
                    break;
                case 'health':
                    this.props.heal('player', entity.health);
                    this.props.removeEntity(entityName);
                    this.props.move('player', vector);
                    break;
                case 'exit':
                    this.props.resetBoard();
                    this.props.setMap(this.props.mapAlgo1());
                    this.props.setLocation('player', this._getEmptyCoords());
                    this.props.increaseLevel();
                    this._fillMap();
                    break;
                default:
                    break;
            }
        }
        return;
    }
    
    
    render() {
    
        const {map, entities, occupiedSpaces, level, player, windowHeight,
                windowWidth, winner, darkness} = this.state,
              SIGHT = 7,
              tileSize = 10;
        
        console.log("windowWidth: " + map.length);
        // Get start coords for current viewport
        const numCols = Math.floor((windowWidth / tileSize) - 5),
              numRows = Math.floor((windowHeight / tileSize) - 17);
        console.log("numCols: " +numCols+ " numRows: " + numRows);
        let startX    = Math.floor(player.x - (numCols / 2));
        let startY    = Math.floor(player.y - (numRows / 2));
        // Make sure start isn't less than 0
        if (startX < 0) startX = 0;
        if (startY < 0) startY = 0;
        // Set end coords
        let endX = startX + numCols;
        let endY = startY + numRows;
        // Final validation of start and end coords
        console.log("this is map\n" + map + "\nmore map stuff");
        
        if (endX > map.length) {
            startX = numCols > map.length ? 0 : startX - (endX - map.length);
            endX   = map.length;
        }
        if (endY > map[0].length) {
            startY = numRows > map[0].length ? 0 : startY - (endY - map[0].length);
            endY   = map[0].length;
        }
        
        // Create visible gameboard
        let rows = [], tileClass, row;
        for (let y = startY; y < endY; y++) {
            row = [];
            for (let x = startX; x < endX; x++) {
                let entity = occupiedSpaces[`${x}x${y}`];
                if (!entity) {
                    tileClass = reverseLookup[map[x][y]];
                } else {
                    tileClass = entities[entity].entityType;
                }
                if (darkness) {
                    // check if it should be dark
                    const xDiff = player.x - x,
                          yDiff = player.y - y;
                    if (Math.abs(xDiff) > SIGHT || Math.abs(yDiff) > SIGHT) {
                        tileClass += ' dark';
                    } else if (Math.sqrt(Math.pow(xDiff, 2) + Math.pow(yDiff, 2)) >= SIGHT) {
                        tileClass += ' dark';
                    }
                }
                //row.push('<span className="tile "' + tileClass + ' key= ' + x + 'x' + y +'></span>');
                row.push(React.createElement('span', {className: 'tile ' + tileClass, key: x + 'x' + y}, ' '));
            }
            //rows.push('<div className="boardRow"' + ' key: row' + y + '></div>');
            rows.push(React.createElement('div', {className: 'boardRow', key: 'row' + y}, row));
        }
        
        
        return (
          <div id='game'>
              <ul id='ui'>
                  <li id='health'>  <div className="ra-hover col-md-3 col-sm-4"><i className="ra ra-monster-skull"></i> ra-monster-skull </div>
	                  <span className='label'>Health:</span> {player.health}</li>
                  <li id='weapon'><span className='label'>Weapon:</span> {player.weapon}</li>
                  <li id='attack'><span className='label'>Attack:</span> {player.attack}</li>
                  <li id='playerLevel'><span className='label'>Level:</span> {player.level}</li>
                  <li id='xp'><span className='label'>Next Level:</span> {player.toNextLevel} XP</li>
                  <li id='level'><span className='label'>Dungeon:</span> {level}</li>
              </ul>
              <div className = 'buttons'>
                  <ToggleButton
                    label = 'Toggle Darkness'
                    id = 'toggleDarkness'
                    handleClick = {this._toggleDarkness.bind(this)} />
              </div>
            
              <div id='board'>
                  {rows}
              </div>
          </div>
        );
    }
}

function mapStateToProps(state) {
    // remember, you created an item state.posts.post in your reducer literally just for this page when it emits it's
    // action creator to hold the return payload / blog post
    return { state: state };
}



export default connect(mapStateToProps, actions)(Board);



